
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
XHeader, XInput, XButton, Group, Cell, Toast
},
data() {
return {
phoneValue: 0,
code: 0,
showWarn: false,
showWarnTxt: '',
routeName: this.$route.name,
times: 60,
getCodeBtn: false,
}
},
methods: {
getCode() {
console.log(this.phoneValue)
let value = this.phoneValue
if (!value && value == 0) {
this.showWarnTxt = ''
this.showWarn = true
} else if (!(/^1(3|4|5|7|8)\d{9}$/.test(value))) {
this.showWarnTxt = ''
this.showWarn = true
} else {
console.log('ok')
let me = this;
me.getCodeBtn = true;
let interval = window.setInterval(function () {
if ((me.times--) <= 0) {
me.times = 60;
me.getCodeBtn = false;
window.clearInterval(interval);
}
}, 1000);
this.$axios({
method: 'POST',
url: 'http://192.168.0.19/hxjy/weixin/massage',
data: {
phone:this.phoneValue
},
}).then((r) => {
console.log(r)
}).catch((err) => {
console.log(err);
})

}
},
loginSubmit() {
console.log(this.phoneValue)
console.log(this.code)
this.$axios({
method: 'POST',
url: 'http://192.168.0.19/hxjy/weixin/studetLogin',
data: {
phone: this.phoneValue,
code: this.code
},
}).then((r) => {
console.log(r)
}).catch((err) => {
console.log(err);
})
}
},
created() {
document.title = this.$route.name

}
}



